import { useEffect, useState } from "react";
import { connect } from "react-redux";
import { setTotal } from "../../../redux/cartReducer";
import { State } from "../../../redux/Store";
import BuyerDetailsForm, { BuyerValues } from "./BuyerDetailsForm";
import CheckoutForm from "./CheckoutForm";

const PaymentProcess: React.FC<PropsType> = ({total}) => {
  const [step, setStep] = useState(1);
  const [buyerDetails, setBuyerDetails] = useState({}) as any;

    const handleBuyerDetails = (data: BuyerValues) => {
        setBuyerDetails(data)
    }


  return step === 1 ? (
    <BuyerDetailsForm total={total} setStep={setStep} handleBuyerDetails={handleBuyerDetails} />
  ) : (
    <CheckoutForm total={total} buyerDetails={buyerDetails} />
  );
};
type PropsType = {
    total: number
}
const mapStateToProps = (state: State) => ({
    total: state.cart.total
})
export default connect(mapStateToProps)(PaymentProcess);
